@import url(https://fonts.googleapis.com/css2?family=Baloo+2&display=swap);
.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* Search Form */
.search-form {
  width: 500px;
  margin: auto;
}

.search-form input {
  width: 100%;
  padding: 10px 20px;
  border-radius: 5px;
  border: 1px solid #eee;
  box-shadow: 0px 1px 2px #eee;
  font-size: 1em;
  transition: box-shadow 0.4s;
}

.empty-query {
  box-shadow: 0px 0 0px 3px #ff000069 !important;
}

.query-error {
  position: absolute;
}

.search-form button {
  display: none;
}

.search {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.search-button {
  border: 0;
  border-radius: 5px;
  background: #a4724c;
  padding: 10px 25px;
  font-size: 1.1em;
  color: white;
  box-shadow: 0 2px 3px #8a6040;
}

.search-button:hover {
  cursor: pointer;
  background-color: #966845;
}

/* Logo */
.logo svg {
  width: 150px;
  height: 150px;
}
.logo {
  margin-bottom: 20px;
}

/* Category */
.options,
.option {
  display: flex;
  align-items: center;
}

.options {
  margin: 20px 0;
}

.option {
  margin-right: 10px;
}

.check {
  box-shadow: 0px 0px 0px 1px #eee;
  border-radius: 5px;
  padding: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
  transition: box-shadow 0.2s, background-color 0.2s;
  background-color: white;
}

.checked {
  background-color: #a4724c;
  box-shadow: 0 2px 3px #8a6040;
  border: none;
}

.check:hover {
  cursor: pointer;
}

/* Other */
.nothing-found {
  margin-top: 20px;
}

/* Media */
@media (max-width: 600px) {
  .search-form {
    width: 90vw;
  }

  .options {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 90vw;
  }

  .option {
    margin: 5px;
  }
}

@media (max-width: 600px) {
  .logo svg {
    width: 100px;
    height: 100px;
  }
}

/* Search Results */
.search-page {
  padding-bottom: 50px;
  margin-top: 100px;
}

.search-page .loading {
  padding-top: 50px;
}

.results-wrapper {
  margin-top: 50px;
}

.results-header {
  padding: 40px 20px 0 20px;
}

.grid {
  display: grid;
  grid-template-columns: 60% 10% 10% 10% 10%;
}

.torrent {
  background-color: white;
  border-radius: 3px;
  margin: 10px 0;
  padding: 20px;
  box-shadow: 0px 1px 2px 2px #eee;
}

.torrent img {
  width: 15px;
  margin: 0 5px;
}

.torrent-title {
  margin-bottom: 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-weight: 900;
  font-size: 1.2em;
  color: #505050;
}

.torrent-detail {
  display: grid;
  grid-template-columns: 30% 45% 25%;
  /* display: flex;
    justify-content: space-between; */
}

.box {
  display: flex;
  /* justify-content: center; */
  align-items: center;
}

.torrent-category {
  display: flex;
}

.torrent-category div {
  background: #eee;
  box-shadow: 0px 1px 2px #eee;
  padding: 0 5px;
  border-radius: 6px;
  margin-right: 5px;
  font-size: 0.9em;
}

.vip {
  color: #a4724c;
}

.vip img {
  vertical-align: middle;
}

.magnet a {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 5px;
  border-radius: 5px;
  background-color: #4bb49a;
  box-shadow: 0px 1px 2px #4bb49a;
  transition: background-color 0.2s;
}

.magnet a:hover {
  background-color: #3fa088;
}

.mobile,
.torrent-detail .magnet {
  display: none;
}

@media (max-width: 992px) {
  .torrent-detail,
  .change-category {
    display: flex;
    flex-direction: column;
  }

  .change-category {
    align-items: center;
  }

  .torrent-detail div {
    margin: 5px;
  }
}

.nothing-found {
  text-align: center;
  margin-top: 50px;
}

@media (max-width: 800px) {
  .results-wrapper {
    margin-top: 150px;
  }
}

@media (max-width: 600px) {
  .torrent-info {
    width: 100%;
  }

  .results-header {
    display: none;
  }

  .mobile {
    display: block;
    width: 100%;
  }

  .mobile-wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .box {
    display: none;
  }

  .torrent {
    display: flex;
  }

  .torrent-detail .magnet {
    display: block;
  }
}

.change-category {
  display: flex;
  justify-content: space-between;
  margin-left: 16px;
  border-bottom: 1px solid #eee;
}

.number-results,
.change-category ul {
  display: flex;
}

.number-results > *,
.change-category ul li {
  margin: 0px 5px;
}

.change-category ul li:hover {
  cursor: pointer;
}

.current-category {
  background: #a4724c;
  color: white;
  padding: 0 5px;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
}

.pagination {
  display: flex;
  justify-content: flex-end;
}

.pagination ul {
  display: flex;
}

.pagination ul li {
  padding: 5px 15px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 0px 1px 2px #eee;
  margin: 0px 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagination ul li:hover {
  background-color: #eee;
  cursor: pointer;
}

.pagination ul li img {
  height: 15px;
}

.more {
  background-color: #f8f8f8 !important;

  box-shadow: none !important;
}

.current-page {
  background-color: #a4724c !important;
  color: white;
}

.torrent-uploder,
.torrent-date {
  display: flex;
}

/* Torrent */
.torrent-page {
  margin-top: 100px;
}

.torrent-page-main-detail {
  background-color: white;
  border-radius: 10px;
  padding: 30px;
  display: grid;
  grid-template-columns: 80% 20%;
}

.torrent-page-category {
  display: flex;
  margin: 10px 0;
}

.torrent-page-category div {
  background: #eee;
  box-shadow: 0px 1px 2px #eee;
  padding: 0 5px;
  border-radius: 6px;
  margin-right: 5px;
}

.torrent-page-detail {
  display: grid;
  grid-template-columns: auto auto auto auto;
}

.torrent-page-uploder,
.torrent-page-date,
.seed-and-leech {
  display: flex;
  align-items: center;
}

.torrent-page-uploder img,
.torrent-page-date img {
  margin-right: 5px;
}

.torrent-page-magnet a {
  background-color: #4bb49a;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 10px 0;
  color: white;
}

.seed {
  color: #4bb49a;
  margin: 0 5px;
}

.leech {
  color: #c35256;
  margin: 0 5px;
}

.torrent-desciption {
  margin-top: 50px;
}

.description {
  white-space: pre-wrap;
  word-wrap: break-word;
  font-size: 11px;
  font-family: "Courier New", Courier, monospace !important;
  background-color: white;
  border-radius: 5px;
  padding: 10px;
  height: 300px;
  overflow-y: scroll;
}

.torrent-comments {
  margin-top: 50px;
}

.comment {
  background-color: white;
  border-radius: 5px;
  padding: 10px;
  margin: 10px 0;
}
.comment-info {
  display: flex;
  justify-content: space-between;
}

.comment-username {
  color: #a4724c;
}
.comment-date {
  color: #7d7d7d;
}

.size,
.info h2,
.comment .comment-text {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.comment-text {
  white-space: pre-wrap !important;
}

@media (max-width: 992px) {
  .torrent-page-main-detail,
  .torrent-page-detail {
    display: flex;
    flex-direction: column;
  }

  .torrent-page-detail div {
    margin: 10px 0;
  }

  .seed-and-leech div {
    margin: 0 10px 0 0;
  }

  .leech {
    margin-left: 10px !important;
  }

  .torrent-page-magnet a {
    justify-content: center;
  }
}

@media (max-width: 700px) {
  .torrent-page {
    margin-top: 150px;
  }
}

@media (max-width: 800px) {
  .torrent-page {
    margin-top: 150px;
  }
}

.dropdown-menu-open {
  top: 135px;
}

.torrent-page-detail {
  margin-top: 20px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Baloo 2", cursive;
  color: #292929;
  text-decoration: none;
}

*:focus {
  outline: none;
}

ul {
  list-style: none;
}

body {
  background-color: #f8f8f8;
}

/* Spinner */
.loading {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.loader {
  -webkit-animation: spin 1.5s infinite; /* Safari */
  width: 25px;
  height: 25px;
  animation: spin 1.5s infinite;
}

.fa-spinner {
  -webkit-animation: spin 1s infinite;
          animation: spin 1s infinite;
}

/* Safari */
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Container */
.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    width: 670px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 890px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1090px;
  }
}

.pagenotfound {
  text-align: center;
  margin-top: 50px;
}

/* ScrollBard */
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #919191;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #a4724c;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #8d6241;
}

/* Header */
/* Header */
.header {
  background-color: white;
  box-shadow: 0px 1px 2px #eee;
  display: flex;
  align-items: center;
  padding: 10px;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 2;
}

.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .header-logo svg {
  width: 50px;
  height: 50px;
}

.header-logo {
  position: absolute;
  left: 10px;
}

.header form {
  display: flex;
  align-items: center;
}

.header form input {
  padding: 5px 20px;
  border-radius: 5px;
  border: 1px solid #eee;
  box-shadow: 0px 1px 2px #eee;
  font-size: 1em;
  transition: box-shadow 0.4s;
}

.header form button {
  border: 0;
  border-radius: 5px;
  background: #a4724c;
  padding: 5px 25px;
  font-size: 1.1em;
  color: white;
  box-shadow: 0 2px 3px #8a6040;
  margin-left: 10px;
}

.header form button:hover {
  cursor: pointer;
}

.header ul {
  display: flex;
}

.header ul li {
  color: #a4724c;
  padding: 0 5px;
}

.header form button img {
  display: none;
}

.header form button span {
  color: white;
}

@media (max-width: 800px) {
  .header {
    flex-direction: column;
  }
  .header-logo {
    position: relative;
    left: auto;
    margin: 10px 0;
  }
  .header form button img {
    display: block;
  }

  .header form button span {
    display: none;
  }

  .header .container {
    width: 90vw;
  }
}

@media (max-width: 500px) {
  .header {
    padding: 10px 0;
  }
  .header .container {
    width: 100vw;
  }

  .header form button {
    padding: 5px;
  }

  .header form,
  .header form input {
    width: 100%;
  }
}

.go-back {
  color: blue;
}

